import type { Value } from "@ali/4ever-cangjie";

export const isSelectedBlock = (value: Value, targetType) => {
  const { selection, document, isFocused } = value;
  return !!(
    selection
      && isFocused
      && selection.isCollapsed
      // @ts-ignore
      && document.getClosest(selection.anchor.key, ({ type }) => type === targetType)
  );
};
